<template>
    <div class="list">
        <el-row  class="list-row">
            <el-col :span="12" class="list-col">
                <list-name :name="listName" :listProp="$route.params.id" @rename="refetchLists" @delete="refetchAll"></list-name>
                <task-list :tasks="sorted" @taskDelete="refetchTasks" @updateOrder="refetchTasks" :listProp="$route.params.id"></task-list>
                <div style="padding: 10px 10px 0;">
                    <task-adder @newTask="refetchTasks" :listProp="$route.params.id"></task-adder>
                </div>
            </el-col>
        </el-row>
    </div>
            
</template>
<script>
/* components */
import TaskList from '@/components/TaskList.vue'
import TaskAdder from '@/components/TaskAdder.vue'
import ListName from '@/components/ListName.vue'

/* graphql */
import gql from 'graphql-tag'
import TASKS from '@/graphql/task/Tasks.gql'
import ALL_LISTS from '@/graphql/list/Lists.gql'

export default {
    name: 'Lists',
    components: {
        TaskList,
        TaskAdder,
        ListName
    },
    data() {
        return {
            allTasks: [],
            allLists: []
        }
    },
    apollo: {
        allTasks: {
            query: gql`${TASKS}`,
            variables() {
                return {
                    user: this.$root.$data.userId
                }
            }
        },
        allLists: {
            query: gql`${ALL_LISTS}`,
            variables() {
                return {
                    user: this.$root.$data.userId
                }
            }
        }
    },
    computed: {
        sorted() {
            let temp = [];
            this.allTasks.forEach(task => {
                if (task.list && task.list.id === this.$route.params.id) {
                    temp.push(task)
                }
            })
            temp.sort((a, b) => {
                const keyA = a.globalOrder
                const keyB = b.globalOrder

                if(keyA < keyB) return -1;
                if(keyA > keyB) return 1;

                return 0;
            })

            return temp;
        },
        listName() {
            var name = ''
            this.allLists.forEach(list => {
                if (list.id === this.$route.params.id) {
                    name = list.name
                    return
                }
            })

            return name
        }
    },
    methods: {
        refetchTasks() {
            this.$apollo.queries.allTasks.refetch()
        },
        refetchLists() {
            this.$apollo.queries.allLists.refetch()
        },
        refetchAll() {
            this.refetchTasks();
            this.refetchLists();
        }
    }
}
</script>
<style lang="less" scoped>
.list-row {
    h2 {
        padding: 0 10px;
    }
}
</style>

