<template>
    <div class="lists">
        <router-view></router-view>
        <!-- <task-list :tasks="sorted" @taskDelete="refetchTasks" @updateOrder="refetchTasks"></task-list> -->
    </div>
</template>
<script>
/* components */
import TaskList from '@/components/TaskList.vue'

/* graphql */
import gql from 'graphql-tag'
import TASKS from '@/graphql/task/Tasks.gql'

export default {
    name: 'Lists',
    components: {
        TaskList
    },
    data() {
        return {
            allTasks: []
        }
    },
    apollo: {
        allTasks: {
            query: gql`${TASKS}`,
            variables() {
                return {
                    user: this.$root.$data.userId
                }
            }
        }
    },
    computed: {
        sorted() {
            let temp = [];
            this.allTasks.forEach(task => temp.push(task))
            temp.sort((a, b) => {
                const keyA = a.globalOrder
                const keyB = b.globalOrder

                if(keyA < keyB) return -1;
                if(keyA > keyB) return 1;

                return 0;
            })

            return temp;
        }
    },
    methods: {
        refetchTasks() {
            this.$apollo.queries.allTasks.refetch()
        },
    }
}
</script>
