<template>
<div>
    <el-container>
        <el-main>
            <el-card class="card-box">
                <h2>Create new account</h2>
                <el-form label-position="top" label-width="100px" :model="form">
                    <el-form-item label="Email">
                        <el-input type="email" v-model="form.email"></el-input>
                    </el-form-item>
                    <el-form-item label="Password">
                        <el-input type="password" v-model="form.password"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="confirm()">Register</el-button>
                    </el-form-item>
                </el-form>
                <router-link to="/login" tag="div">
                    <el-button type="text">Already registered? Login here</el-button>
                </router-link>
            </el-card>
        </el-main>
    </el-container>
</div>
</template>

<script>
import { GC_USER_ID, GC_AUTH_TOKEN } from '@/constants/settings'
import CREATE_USER_MUTATION from '@/graphql/user/CreateUserMutation.gql'
import SIGNIN_USER_MUTATION from '@/graphql/user/SigninUserMutation.gql'

export default {
    name: 'AppRegister',
    data () {
        return {
            form: {
                email: '',
                password: ''
            },
        }
    },
    methods: {
        confirm () {
            const { email, password } = this.form
            // register
            this.$apollo.mutate({
                mutation: CREATE_USER_MUTATION,
                variables: {
                    email,
                    password
                }
            }).then((result) => {
                const id = result.data.signinUser.user.id
                const token = result.data.signinUser.token
                this.saveUserData(id, token)
                this.$router.push({path: '/app/overview'})
            }).catch((error) => {
                this.$message.error('Error registering')
            })
        },
        saveUserData(id, token) {
            localStorage.setItem(GC_USER_ID, id)
            localStorage.setItem(GC_AUTH_TOKEN, token)
            this.$root.$data.userId = localStorage.getItem(GC_USER_ID)
        }
    }
}
</script>

<style lang="less">
.card-box {
    max-width: 480px;
    width: 100%;
    margin: 0 auto;
}
</style>
