<template>
    <div class="list-adder">
        <el-button type="text" style="font-size: 14px;" v-show="!active" @click="open">+ Add list</el-button>
        <div v-show="active">
            <el-input
                v-model="name"
                placeholder="List name"
                @keyup.esc.native="close"
                @keyup.enter.native="addList"
                ref="listInput">
            </el-input>
            <div>
                <el-button
                    type="primary"
                    size="small"
                    @click="addList">
                    Add list
                </el-button>
                <el-button
                    type="text"
                    size="small"
                    @click="close">
                    Cancel
                </el-button>
            </div>
        </div>
    </div>
</template>
<script>
/* graphql */
import gql from 'graphql-tag'
import ADD_LIST from '@/graphql/list/AddList.gql'

export default {
    data() {
        return {
            active: false,
            name: ''
        }
    },
    methods: {
        open() {
            this.active = true;
            this.$nextTick(_ => {
                this.$refs.listInput.$refs.input.focus();
            });
        },
        close() {
            this.active = false
            this.name = ''
        },
        addList() {
            if (this.name == '') {
                this.close()
                return
            }

            this.$apollo.mutate({
                mutation: gql`${ADD_LIST}`,
                variables: {
                    user: this.$root.$data.userId,
                    name: this.name
                }
            }).then(({ data }) => {
                console.log(data)
                this.$emit('newList', data.createList)
                this.$message({
                    message: 'List added.',
                    type: 'success'
                });
                this.$router.push({ path: `/app/lists/${ data.createList.id }` })
                this.close();
            })
        }
    }
}
</script>
<style lang="less" scoped>
.list-adder {
    padding: 0 20px;

    .el-input {
        margin-bottom: 8px;
    }
}
</style>

