<template>
    <div>
        <el-button type="text" style="font-size: 14px;" v-show="!active" @click="open">+ Add task</el-button>
        <div v-show="active">
            <el-row
                type="flex"
                justify="space-between"
                style="margin-bottom: 8px;">
                <!-- <el-col :span="18"> -->
                    <el-input
                        style="margin-right: 4px;"
                        size="small"
                        type="text"
                        placeholder="Task name"
                        v-model="name"
                        @keyup.esc.native="close"
                        @keyup.enter.native="addTask(listProp)"
                        ref="taskInput">
                    </el-input>
                <!-- </el-col>
                <el-col :span="5" style="display: flex; justify-content: flex-end;"> -->
                    <el-date-picker
                        size="small"
                        type="date"
                        
                        v-model="date"
                        format="dd/MM"
                        :picker-options="dateOptions">
                    </el-date-picker>
                <!-- </el-col> -->
            </el-row>
            <el-row type="flex">
                <el-col :span="17" style="padding: 0; display: flex; justify-content: space-between; align-items: center;">
                    <div>
                        <el-button
                            type="primary"
                            size="small"
                            @click="addTask(listProp)">
                            Add task
                        </el-button>
                        <el-button
                            type="text"
                            size="small"
                            @click="close">
                            Cancel
                        </el-button>
                    </div>
                    <div>
                        <el-tag
                            v-if="list && listProp === null"
                            @close="removeList"
                            closable size="small"
                            type="primary"
                            style="margin-left: 5px;">
                            {{ list.name }}
                        </el-tag>

                        <el-tag
                            v-if="listId"
                            @close="removeList"
                            closable size="small"
                            type="primary"
                            style="margin-left: 5px;">
                            {{ listModel }}
                        </el-tag>

                        <el-tag
                            v-for="label in taskLabels"
                            :key="label.name"
                            @close="removeLabel"
                            closable size="small"
                            type="info"
                            style="margin-left: 5px;">
                            {{ label.name }}
                        </el-tag>
                    </div>
                </el-col>
                <el-col :span="7" style="display: flex; justify-content: flex-end; padding: 0; align-items: center;">
                    <div style="margin-right: 10px;">
                        <el-button
                            v-show="!listAutocompleteActive && !labelAutocompleteActive && listProp === null"
                            type="text"
                            size="mini"
                            @click="showListAutocomplete"
                            style="font-size: 14px; margin-right: 10px;">
                            + List
                        </el-button>
                        <el-autocomplete
                            v-show="listAutocompleteActive && !labelAutocompleteActive"
                            :fetch-suggestions="listSearch"
                            size="mini"
                            style="width: 108px"
                            ref="listAutocomplete"
                            v-model="listModel"
                            placeholder="Add/Select list"
                            @blur="closeAutocomplete"
                            @select="selectList"
                            @keyup.esc.native="closeAutocomplete"
                            :select-when-unmatched="true"
                            :value-key="'name'">
                        </el-autocomplete>
                        
                        <el-button
                            v-show="!listAutocompleteActive && !labelAutocompleteActive"
                            type="text"
                            size="mini"
                            @click="showLabelAutocomplete" style="font-size: 14px; margin-right: 5px;">
                            + Label
                        </el-button>
                        <el-autocomplete
                            v-show="!listAutocompleteActive && labelAutocompleteActive"
                            :fetch-suggestions="labelSearch"
                            v-model="label"
                            size="mini"
                            style="width: 108px"
                            ref="labelAutocomplete"
                            placeholder="Add/Select label"
                            @blur="closeAutocomplete"
                            @select="selectLabel"
                            @keyup.esc.native="closeAutocomplete"
                            :select-when-unmatched="true"
                            :value-key="'name'">
                        </el-autocomplete>
                    </div>
                    <i class="el-icon-edit-outline" style="margin-right: 15px; color: rgb(198, 209, 222); font-size: 20px;"></i>
                    <!-- <el-rate
                        @change="setImportance"
                        v-model="importance"
                        :max="3"
                        :low-threshold="1"
                        :high-threshold="3"
                        :icon-classes="['el-icon-warning', 'el-icon-warning', 'el-icon-warning']"
                        :void-icon-class="'el-icon-warning'"
                        :colors="['#FFEB3B', '#FF9800', '#F44336']">
                    </el-rate> -->
                </el-col>
            </el-row>
        </div>
    </div>
</template>
<script>
/* graphql */
import gql from 'graphql-tag'
import TASKS from '@/graphql/task/Tasks.gql'
import LABELS from '@/graphql/label/Labels.gql'
import LISTS from '@/graphql/list/Lists.gql'
import ADD_TASK from '@/graphql/task/AddTask.gql'

/* event-bus */
import { EventBus } from '@/event-bus.js';

export default {
    name: 'TaskAdder',
    props: {
        dateType: {
            default: null
        },
        listProp: {
            default: null
        },
        inbox: {
            default: false
        }
    },
    apollo: {
        allLabels: {
            query: gql`${LABELS}`,
            variables() {
                return {
                    user: this.$root.$data.userId
                }
            }
        },
        allLists: {
            query: gql`${LISTS}`,
            variables() {
                return {
                    user: this.$root.$data.userId
                }
            }
        },
        allTasks: {
            query: gql`${TASKS}`,
            variables() {
                return {
                    user: this.$root.$data.userId
                }
            }
        }
    },
    data() {
        return {
            allLabels: [],
            allLists: [],
            allTasks: [],
            taskLabels: [],
            active: false,
            listAutocompleteActive: false,
            labelAutocompleteActive: false,
            name: '',
            date: '',
            label: '',
            listModel: null,
            list: null,
            listId: null,
            importance: null,
            oldImportance: null,
            dateOptions: {
                shortcuts: [{
                    text: 'Today',
                    onClick(picker) {
                        let date = new Date()
                        date.setHours(0,0,0,0)
                        picker.$emit('pick', new Date())
                    }
                },
                {
                    text: 'Tomorrow',
                    onClick(picker) {
                        let date = new Date()
                        date.setDate(date.getDate() + 1)
                        date.setHours(0,0,0,0)
                        picker.$emit('pick', date)
                    }
                },
                ...this.nextSevenDays()
                ]
            }
        }
    },
    computed: {
        defaultDate() {
            let date = new Date()
            if (this.dateType === 'today') {
                date.setHours(0,0,0,0)
                return date;
            } else if (this.dateType === 'tomorrow') {
                date.setDate(date.getDate() + 1)
                date.setHours(0,0,0,0)
            } else if (this.dateType === 'next7days') {
                date.setDate(date.getDate() + 2)
                date.setHours(0,0,0,0)
            } else {
                date = null
            }

            return date
        },
        dateTimestamp() {
            if (this.date == null) {
                if (this.defaultDate !== null) {
                    return this.defaultDate
                }
                return null
            } else {
                return (this.date)
            }
        },

        getImportance() {
            switch(this.importance) {
                case 3:
                    return 'IMP_3'
                    break
                case 2:
                    return 'IMP_2'
                    break
                case 1:
                    return 'IMP_1'
                    break
                default:
                    return 'IMP_0'
            }
        },

        labelIds() {
            let labelIds = []
            
            this.taskLabels.forEach(label => {
                if (label.id !== undefined) labelIds.push(label.id)
            })

            if (labelIds.length == 0) return undefined
            
            return labelIds
        },
        newLabels() {
            let labels = []
            this.taskLabels.forEach(label => {
                if (label.id === undefined) {
                    label.userId = this.$root.$data.userId
                    labels.push(label)
                }
            })
            if (labels.length == 0) return undefined
            return labels
        }
    },
    methods: {
        nextSevenDays() {
            let shortcuts = [];
            for (var index of this.$lodash.range(2,7)) {
                shortcuts.push({
                    text: this.$moment().add(index, 'days').calendar(null, {
                        nextWeek: 'dddd',
                    }),
                    onClick(picker) {
                        let date = new Date()
                        date.setDate(date.getDate() + index)
                        date.setHours(0,0,0,0)
                        picker.$emit('pick', date)
                    }
                })
            }

            return shortcuts
        },
        setImportance() {
            if (this.importance == this.oldImportance) {
                this.importance = null
                this.oldImportance = null
            } else {
                this.oldImportance = this.importance
            }
        },
        removeList(list) {
            this.list = null
            this.listId = null
            this.listModel = null
        },
        removeLabel(label) {
            this.taskLabels.splice(this.taskLabels.indexOf(label), 1)
        },
        closeAutocomplete() {
            this.listAutocompleteActive = false
            this.labelAutocompleteActive = false
        },
        selectList(item) {
            if (item.id == undefined) {
                this.listId = null
                this.list = { name: item.value, userId: this.$root.$data.userId }
            } else {
                this.listId = item.id
                this.list = null
            }

            this.closeAutocomplete()
            this.label = ''
        },
        selectLabel(item) {
            if (item.name == undefined) this.taskLabels.push({ name: item.value })
            else this.taskLabels.push(item)
            this.closeAutocomplete()
            this.label = ''
        },
        listSearch(queryString, cb) {
            var list = this.allLists;
            var results = queryString ? list.filter(this.createFilter(queryString)) : list;
            cb(results);
        },
        labelSearch(queryString, cb) {
            var labels = this.allLabels;
            var results = queryString ? labels.filter(this.createFilter(queryString)) : labels;
            cb(results);
        },
        createFilter(queryString) {
            return (item) => {
                return (item.name.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
            };
        },
        addTask(listId = null) {
            if (this.name == '') {
                this.close()
                return
            }

            if (listId === null) {
                listId = this.listId
            }

            this.$apollo.mutate({
                mutation: gql`${ADD_TASK}`,
                variables: {
                    user: this.$root.$data.userId,
                    name: this.name,
                    dueDate: this.dateTimestamp,
                    labelsIds: this.labelIds,
                    labels: this.newLabels,
                    importance: this.getImportance,
                    list: this.list,
                    listId: listId,
                    globalOrder: this.allTasks.length,
                    inbox: this.inbox
                }
            }).then(({ data })=> {
                this.$emit('newTask', data.createTask)
                this.$message({
                    message: 'Task added.',
                    type: 'success'
                });
            })
            this.close()
        },
        open() {
            EventBus.$emit('task-adder-open')
            this.active = true
            this.$nextTick(_ => {
                this.$refs.taskInput.$refs.input.focus();
            });
        },
        close() {
            this.active = false
            this.name = ''
            this.date = this.defaultDate
            this.importance = null
            this.list = null
            this.listId = null
            this.listModel = null
            this.taskLabels = []
        },
        showListAutocomplete() {
            this.listAutocompleteActive = true;
            this.$nextTick(_ => {
                this.$refs.listAutocomplete.$refs.input.focus();
            });
        },
        showLabelAutocomplete() {
            this.labelAutocompleteActive = true;
            this.$nextTick(_ => {
                this.$refs.labelAutocomplete.$refs.input.focus();
            });
        }
    },
    mounted() {
        this.date = this.defaultDate
        EventBus.$on('task-adder-open', _ => {
            this.close()
        })
    }
}
</script>
<style lang="less">
    .el-rate {
        span i {
            margin-right: 0;
        }
        span {
            margin-right: 6px;;
        }
        span:last-child {
            margin-right: 0;
        }
    }
</style>

