<template>
    <div class="main">
        <el-container>
            <el-header height="40px">
                <div class="branding">
                    <router-link to="/app/overview" tag="h3">
                        TODO APP
                    </router-link>
                </div>
                <div class="header-navigation">
                    <!-- <router-link to="/login" tag="span">
                        <el-button size="mini">Log in</el-button>
                    </router-link> -->
                    <el-button @click="logout()" size="mini" type="text">Log out</el-button>
                </div>
            </el-header>

            <el-container>
                <el-aside width="180px">
                    <el-menu>
                        <router-link to="/app/overview" tag="div">
                            <el-menu-item index="1">
                                <span>Overview</span>
                            </el-menu-item>
                        </router-link>

                        <router-link :to="'/app/lists/' + list.id" tag="div" v-for="(list, index) in allLists" :key="list.id">
                            <el-menu-item :index="(index+2).toString()">
                                <span>{{ list.name }}</span>
                            </el-menu-item>
                        </router-link>

                        <list-adder @newList="refetchLists"></list-adder>

                        <!-- <el-menu-item index="300">
                            
                        </el-menu-item> -->

                    </el-menu>
                </el-aside>

                <el-main class="list-main">
                    <router-view></router-view>
                </el-main>
            </el-container>
        </el-container>
        <div class="inbox-bg" :class="{ 'active': getInboxState == 'opened', 'active-dragging': getInboxState == 'dragging' }" @click="inboxTrigger"></div>
        <div id="inbox" class="list-col" :class="{ 'active': getInboxState == 'opened', 'active-dragging': getInboxState == 'dragging' }">
            <!-- <div class="tooltip">
                <p>No time to schedule your task? We got you covered. Use <strong>Inbox</strong> for quickly creating and saving your tasks for later.</p>
                <el-button type="text" icon="el-icon-circle-close"></el-button>
            </div> -->
            <h2>Inbox</h2>
            <div class="task-wrapper">
                <div v-bar>
                    <task-list style="max-height: calc(100vh - 122px)" :tasks="inboxTasks()" @taskDelete="refetchTasks" @updateOrder="refetchTasks" group="overview"></task-list>
                </div>
                <div style="padding: 0px 10px 0;">
                    <task-adder :inbox="true" @newTask="refetchTasks"></task-adder>
                </div>
            </div>
        </div>
        <div class="inbox-button" :class="{ 'active': getInboxState == 'opened', 'active-dragging': getInboxState == 'dragging' }">
            <el-badge :value="inboxTasks().length" :hidden="inboxTasks().length === 0">
                <el-button type="primary" icon="el-icon-tickets" circle @click="inboxTrigger"></el-button>
            </el-badge>
        </div>
    </div>
</template>
<script>
/* components */
import ListAdder from '@/components/ListAdder.vue'
import TaskAdder from '@/components/TaskAdder.vue'
import TaskList from '@/components/TaskList.vue'

/* graphql */
import gql from 'graphql-tag'
import ALL_LISTS from '@/graphql/list/Lists.gql'
import TASKS from '@/graphql/task/Tasks.gql'
import DELETE_TASK from '@/graphql/task/DeleteTask.gql'

/* event-bus */
import { EventBus } from '@/event-bus.js';

/* contants */
import { GC_USER_ID, GC_AUTH_TOKEN } from '@/constants/settings.js'

import Vuex from 'vuex'
import { mapMutations, mapGetters } from 'vuex'

export default {
    components: {
        ListAdder,
        TaskAdder,
        TaskList
    },
    apollo: {
        allLists: {
            query: gql`${ALL_LISTS}`,
            variables() {
                return {
                    user: this.$root.$data.userId
                }
            }
        },
        allTasks: {
            query: gql`${TASKS}`,
            variables() {
                return {
                    user: this.$root.$data.userId
                }
            }
        },
    },
    data() {
        return {
            allLists: [],
            allTasks: [],
            inboxActive: false
        }
    },
    computed: {
        userId () {
            return this.$root.$data.userId
        },
        ...mapGetters([
            'getInboxState'
        ])
    },
    methods: {
        inboxTrigger() {
            if (this.getInboxState === 'closed') {
                this.openInbox()
            } else {
                this.closeInbox();
            }
        },
        ...mapMutations([
            'openInbox',
            'closeInbox',
            'dragInbox'
        ]),
        inboxTasks() {
            return this.allTasks.filter(task => task.inbox);
        },
        refetchTasks() {
            this.$apollo.queries.allTasks.refetch()
        },
        logout() {
            localStorage.removeItem(GC_USER_ID)
            localStorage.removeItem(GC_AUTH_TOKEN)
            this.$root.$data.userId = localStorage.getItem(GC_USER_ID)
            this.$router.push({path: '/login'})
        },
        refetchLists() {
            this.$apollo.queries.allLists.refetch()
        }
    },
    mounted() {
        const subQuery = gql`subscription listCreated($userId: ID) {
            List(filter: {
                mutation_in: [CREATED, DELETED, UPDATED]
                node: {
                user: {
                    id: $userId
                }
                }
            }) {
                mutation node {
                    id
                }
            }
        }`

        const observer = this.$apollo.subscribe({
            query: subQuery,
            variables: {
                userId: this.$root.$data.userId,
            },
        })

        observer.subscribe({
            next: (data) => {
                this.refetchLists()
            },
            error(error) {
                console.error(error)
            },
        })
    }
}
</script>

