import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        allTasks: [],
        inboxState: 'closed',
    },
    getters: {
        getAllTasks(state) {
            state.allTasks.sort((a, b) => {
                const keyA = a.globalOrder
                const keyB = b.globalOrder

                if(keyA < keyB) return -1;
                if(keyA > keyB) return 1;

                return 0;
            })

            return state.allTasks;
        },
        getInboxState(state) {
            return state.inboxState
        }
    },
    mutations: {
        addTask(state) {

        },
        closeInbox(state) {
            state.inboxState = 'closed'
        },
        dragInbox(state) {
            state.inboxState = 'dragging'
        },
        openInbox(state) {
            state.inboxState = 'opened'
        } 
    },
    actions: {
        refetchTasks(context) {
            
        },
    }
})
