import Vue from 'vue'
import VueApollo from 'vue-apollo'
import createApolloClient from './apollo'

// Install the vue plugin
Vue.use(VueApollo)

// Config
const options = {
  ssr: false,
  base: process.env.VUE_APP_GRAPHQL_ENDPOINT || '',
  endpoints: {
    graphql: process.env.VUE_APP_GRAPHQL_PATH || 'https://api.graph.cool/simple/v1/cjen5toa33onp01389lmm0n8t',
    subscription: process.env.VUE_APP_GRAPHQL_SUBSCRIPTIONS_PATH || 'wss://subscriptions.graph.cool/v1/cjen5toa33onp01389lmm0n8t',
  },
  persisting: false,
}

// Create apollo client
export const apolloClient = createApolloClient(options)

// Create vue apollo provider
export const apolloProvider = new VueApollo({
  defaultClient: apolloClient,
})
