<template>
    <div class="list-name">
        <h2 v-if="!editing">
            <span>{{ name }}</span>
            <el-dropdown>
                <i class="el-icon-more"></i>
                <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item @click.native="open">Rename</el-dropdown-item>
                    <el-dropdown-item @click.native="deleteList">Delete</el-dropdown-item>
                </el-dropdown-menu>
            </el-dropdown>
        </h2>
        <div v-if="editing" class="list-name-editor">
            <el-input
                type="text"
                class="list-name-input"
                v-model="newName"
                @keyup.native.esc="close"
                @keyup.native.enter="rename"
                ref="listName"
            ></el-input>
            <el-button type="primary" @click="rename">Rename</el-button>
            <el-button
                type="text"
                @click="close">Cancel</el-button>
        </div>
    </div>
</template>
<script>
/* graphql */
import gql from 'graphql-tag'
import UPDATE_LIST from '@/graphql/list/UpdateList.gql'
import DELETE_LIST from '@/graphql/list/DeleteList.gql'

export default {
    props: ['name', 'listProp'],
    data() {
        return {
            editing: false,
            newName: ''
        }
    },
    methods: {
        rename() {
            if (this.name === this.newName) {
                this.close()
            }
            this.$apollo.mutate({
                mutation: gql`${UPDATE_LIST}`,
                variables: {
                    listId: this.listProp,
                    name: this.newName
                }
            }).then(() => {
                this.$emit('rename')
                this.close()
            })
        },
        close() {
            this.editing = false
        },
        open() {
            this.editing = true
            this.newName = this.name

            this.$nextTick(_ => {
                this.$refs.listName.$refs.input.focus();
            });
        },
        deleteList() {
            this.$confirm(`Are you sure you want to delete ${ this.name } and all related tasks?`, 'Danger zone', {
                confirmButtonText: 'Delete',
                cancelButtonText: 'Cancel',
                type: 'warning'
            }).then(_ => {
                this.$apollo.mutate({
                mutation: gql`${DELETE_LIST}`,
                variables: {
                    listId: this.listProp,
                }
            }).then(() => {
                this.$emit('delete');
                this.$router.push({ name: 'Overview' });
                this.close()
            })
            })
            .catch(() => {})
        }
    }
}
</script>
