    .list-name-editor {
        display: flex;
        align-items: center;

        .el-input {
            margin: 10px 10px 10px 0;
        }

        .el-input__inner {
            padding: 0 9px;
        }

        input[type="text"] {
            font-size: 24px;
            font-weight: bold !important;
            font-family: 'Roboto', sans-serif;
            color: #000000
        }

        .list-name-input {
            width: 300px;
        }
        
    }
    
    h2 {
        padding: 0 10px;
        line-height: 40px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .el-dropdown {
            font-size: 24px;
        }

        i {
            margin-left: 5px;
            opacity: 0.2;
            transition: opacity 0.3s ease-in-out;
