<template>
    <div class="task-list">
        
        <draggable v-model="tasks" :move="move" :options="{ forceFallback: true, group: this.group || '' }" @start="startDragging" @end="stopDragging" :class="{ 'drag': drag }" @add="itemAdded">
            <!-- <transition-group name="slide-fade" tag="div"> -->
            <div class="text item" v-for="task in tasks" :key="task.id" :class="{ 'childs': task.parent !== null, 'done': task.done, 'overdue': $moment(task.dueDate).isBefore($moment().add(0, 'days'), 'day') }">
                <div>
                    <el-checkbox :checked="task.done" @change="taskDone($event, task)">{{ task.name }}</el-checkbox>
                </div>
                <div class="options-wrapper">
                    <i class="el-icon-warning" :class="task.importance"></i>
                    <div style="width: 70px; text-align: right">
                        <el-tag size="mini" v-if="task.list">{{ task.list.name }}</el-tag>
                    </div>
                    <div style="width: 70px;">
                        <el-tag size="mini" type="info" v-for="label in task.labels" :key="label.id">{{ label.name }}</el-tag>
                    </div>
                    <span class="time">{{ getTime(task.dueDate) }}</span>
                    <i class="el-icon-error" @click="deleteTask(task)" style="opacity: 0.5" v-if="task.done"></i>
                    <el-dropdown v-if="!task.done">
                        <i class="el-icon-more"></i>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item @click.native="deleteTask(task)">Delete</el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                </div>
            </div>
            <!-- </transition-group> -->
        </draggable>
        
    </div>
</template>
<script>
/* graphql */
import gql from 'graphql-tag'
import TASKS from '@/graphql/task/Tasks.gql'
import DELETE_TASK from '@/graphql/task/DeleteTask.gql'
import UPDATE_TASK from '@/graphql/task/UpdateTask.gql'
import UPDATE_TASK_GLOBAL_ORDER from '@/graphql/task/UpdateTaskGlobalOrder.gql'

/* moment */
import moment from 'moment'

/* draggable */
import draggable from 'vuedraggable'

/* 2d-css-matrix-parse */
import matrix from '2d-css-matrix-parse'

export default {
    name: 'TaskList',
    props: ['tasks', 'group'],
    components: {
        draggable
    },
    apollo: {
        allTasks: {
            query: gql`${TASKS}`,
            variables() {
                return {
                    user: this.$root.$data.userId
                }
            },
            update(data) {
                return data.allTasks
            }
        }
    },
    data() {
        return {
            drag: false,
            insertChild: false,
            allowChild: true,
            allTasks: [],
            startDraggingGlobalOrder: null,
        }
    },
    methods: {
        itemAdded(event) {
            // console.log(event)
        },  
        mouseMoveCallback(event) {
            var dragFallbackElem = document.querySelector('.sortable-drag')
            var dragGhostElem = document.querySelector('.sortable-ghost')
            var offsetX = matrix.fromElement(dragFallbackElem)[4]

            // if (offsetX > 25 && this.allowChild) {
            //     this.insertChild = true
            //     dragGhostElem.style.marginLeft = '25px'
            // } else {
            //     this.insertChild = false
            //     dragGhostElem.style.marginLeft = '0px'
            // }
        },
        startDragging(event) {
            this.drag = true
            document.addEventListener('mousemove', this.mouseMoveCallback)
            this.startDraggingGlobalOrder = this.tasks[event.oldIndex].globalOrder;
        },
        stopDragging(event) {
            this.drag = false
            document.removeEventListener('mousemove', this.mouseMoveCallback)
            if (event.oldIndex > event.newIndex) {
                const stopDraggingGlobalOrder = this.tasks[event.newIndex + 1].globalOrder;
                const id = this.tasks[event.newIndex].id;

                this.updateOrder(id, stopDraggingGlobalOrder);
                
                this.allTasks.forEach(task => {
                    if ( (task.id !== id) && (task.globalOrder >= stopDraggingGlobalOrder) && (task.globalOrder < this.startDraggingGlobalOrder) ) {
                        this.updateOrder(task.id, (task.globalOrder + 1))
                    }
                })
            }


            if (event.oldIndex < event.newIndex) {
                const stopDraggingGlobalOrder = this.tasks[event.newIndex - 1].globalOrder;
                const id = this.tasks[event.newIndex].id;

                this.updateOrder(id, stopDraggingGlobalOrder);
                this.allTasks.forEach(task => {
                    if ( (task.id !== id) && (task.globalOrder <= stopDraggingGlobalOrder) && (task.globalOrder > this.startDraggingGlobalOrder) ) {
                        this.updateOrder(task.id, (task.globalOrder - 1))
                    }
                })
            }
        },
        move(a, b) {
            // console.log(a.draggedContext)
            // console.log(this.insertChild)
            console.log(a,b)
            // return false;
        },
        updateOrder(id, globalOrder) {
            this.$apollo.mutate({
                mutation: gql`${UPDATE_TASK_GLOBAL_ORDER}`,
                variables: {
                    globalOrder,
                    id
                }
            }).then(data => {
                this.$emit('updateOrder')
            }).catch(err => {
                console.log(err)
            })
        },
        taskDone(value, task) {
            this.$apollo.mutate({
                mutation: gql`${UPDATE_TASK}`,
                variables: {
                    done: value,
                    id: task.id
                }
            }).then(data => {
                this.$emit('taskDelete')
            }).catch(err => {
                console.log(err)
            })
        },
        getTime(timestamp) {
            if (timestamp == null) {
                return ''
            }

            return moment(timestamp).format('MMM DD')
        },
        deleteTask(task) {
            const deletedGlobalOrder = task.globalOrder;

            this.$apollo.mutate({
                mutation: gql`${DELETE_TASK}`,
                variables: {
                    id: task.id,
                }
            }).then(data => {
                this.allTasks.forEach(task_temp => {
                    if ( (task_temp.id !== task.id) && (task_temp.globalOrder > task.globalOrder) ) {
                        this.updateOrder(task_temp.id, task_temp.globalOrder - 1)
                    }
                })
                this.$emit('taskDelete')
                this.$message({
                    message: 'Task deleted.',
                    type: 'success'
                });
            })
        }
    }
}
</script>
