        <el-row class="full-height">
            <el-col :span="24" class="full-height">
                <el-row class="list-row">
                    <el-col :span="12" class="list-col">
                        <h2>Today</h2>
                        <!-- <el-button @click="openSpan = (openSpan === 6) ? 0 : 6">Click me</el-button> -->
                        <div class="task-wrapper">
                                <div v-bar>
                                    <task-list :tasks="tasksInDaysFromNow(0, true)" @taskDelete="refetchTasks" @updateOrder="refetchTasks" group="overview"></task-list>
                                </div>
                                <div style="padding: 0px 10px 10px;">
                                    <task-adder @newTask="refetchTasks" dateType="today"></task-adder>
                                </div>
                        </div>
                    </el-col>
                    <el-col :span="12" class="list-col">
                        <h2>Next 7 days</h2>
                        <div v-for="index in $lodash.range(2, 7)" :key="index" v-if="tasksInDaysFromNow(index).length > 0">
                            <div class="day-split" >
                                <span class="day-split--title">{{ $moment().add(index, 'days').format('dddd') }} - {{ $moment().add(index, 'days').format('MMM DD') }}</span>
                            </div>
                            <div class="task-wrapper">
                                <div v-bar>
                                    <task-list :tasks="tasksInDaysFromNow(index)" @taskDelete="refetchTasks"  @updateOrder="refetchTasks" group="overview"></task-list>
                                </div>
                            </div>
                        </div>
                        <div class="task-wrapper">
                            <div style="padding: 0px 10px 0;">
                                <task-adder @newTask="refetchTasks" dateType="next7days"></task-adder>
                            </div>
                        </div>
                    </el-col>
                </el-row>
                <el-row class="list-row">
                    <el-col :span="12" class="list-col" style="border-top: 1px solid #f2f2f2;">
                        <h2>Tomorrow</h2>
                        <div class="task-wrapper">
                            <div v-bar>
                                <task-list :tasks="tasksInDaysFromNow(1)" @taskDelete="refetchTasks" @updateOrder="refetchTasks" group="overview"></task-list>
                            </div>
                            <div style="padding: 0px 10px 0;">
                                <task-adder @newTask="refetchTasks" dateType="tomorrow"></task-adder>
                            </div>
                        </div>
                    </el-col>
                    <el-col :span="12" class="list-col" style="border-top: 1px solid #f2f2f2;">
                        <h2>Urgent &amp; Important</h2>
                        <!-- <h2>Unscheduled</h2> -->
                        <div class="task-wrapper">
                            <div v-bar>
                                <task-list :tasks="tasksInDaysFromNow()" @taskDelete="refetchTasks" @updateOrder="refetchTasks" group="overview"></task-list>
                            </div>
                            <div style="padding: 0px 10px 0;">
                                <task-adder @newTask="refetchTasks"></task-adder>
                            </div>
                        </div>
                    </el-col>
                </el-row>
            </el-col>
            <!-- <el-col :span="6" class="full-height list-col">
                <div class="tooltip">
                    <p>No time to schedule your task? We got you covered. Use <strong>Inbox</strong> for quickly creating and saving your tasks for later.</p>
