                        <task-adder @newTask="refetchTasks"></task-adder>
                    </div>
                </div>
            </el-col> -->
        </el-row>
    </div>
</template>
<script>
/* components */
import TaskAdder from '@/components/TaskAdder.vue'
import TaskList from '@/components/TaskList.vue'
import VueScrollbar from 'vue2-scrollbar';
// require("vue2-scrollbar/style/vue2-scrollbar.css")

/* graphql */
import gql from 'graphql-tag'
import TASKS from '@/graphql/task/Tasks.gql'
import DELETE_TASK from '@/graphql/task/DeleteTask.gql'

/* event-bus */
import { EventBus } from '@/event-bus.js';

export default {
    name: 'Overview',
    components: {
        TaskAdder,
        TaskList,
        VueScrollbar
    },
    apollo: {
        allTasks: {
            query: gql`${TASKS}`,
            variables() {
                return {
                    user: this.$root.$data.userId
                }
            }
        },
    },
    data() {
        return {
            allTasks: [],
        }
    },
    methods: {
        tasksInDaysFromNow(day = null, before = false) {
            let days = [];
            this.allTasks.forEach(task => {
                if (day !== null) {
                    if (before) {
                        if (this.$moment(task.dueDate).isSameOrBefore(this.$moment().add(day, 'days'), 'day')) {
                            days.push(task)
                        }
                    } else {
                        if (this.$moment(task.dueDate).isSame(this.$moment().add(day, 'days'), 'day')) {
                            days.push(task)
                        }
                    }
                } else {
                    if (task.dueDate === null) {
                        days.push(task)
                    }
                }
            })

            days.sort((a, b) => {
                const keyA = a.globalOrder
                const keyB = b.globalOrder

                if(keyA < keyB) return -1;
                if(keyA > keyB) return 1;

                return 0;
            })

            return days.filter(task => !task.inbox)
        },
        refetchTasks() {
            this.$apollo.queries.allTasks.refetch()
        },
    },
    mounted() {
        const subQuery = gql`subscription taskCreated($userId: ID) {
            Task(filter: {
                mutation_in: [CREATED, DELETED, UPDATED]
                node: {
                user: {
                    id: $userId
                }
                }
            }) {
                mutation node {
                id
                }
            }
        }`

        const observer = this.$apollo.subscribe({
            query: subQuery,
            variables: {
                userId: this.$root.$data.userId,
            },
        })

        observer.subscribe({
            next: (data) => {
                this.refetchTasks()
            },
            error(error) {
                console.error(error)
            },
        })
    }
}
</script>

